.dashboard {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px;
  
    &-section {
      flex: 1 1 45%;
      background: #f7f7f7;
      border: 2px solid #f7f7f7;
      border-radius: 10px;
      padding: 20px;
      box-shadow: 0 0 10px #9a9999;
      display: flex;
      flex-direction: column;
      align-items: center;
  
      &__title {
        color: #000000;
        margin-bottom: 10px;
      }
  
      &__file-input {
        margin-bottom: 20px;
      }
  
      &__image-preview {
        width: 100%;
        height: 400px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
  
      &__image-container {
        position: relative;
        width: 400px;
        height: 400px;
        background: repeating-linear-gradient(
            to right,
            #444 0,
            #444 1px,
            transparent 1px,
            transparent 20px
          ),
          repeating-linear-gradient(
            to bottom,
            #444 0,
            #444 1px,
            transparent 1px,
            transparent 20px
          );
        border: 2px solid #6c6c6c;
  
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
  
        .grid-overlay {
          position: absolute;
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0.1);
          z-index: 1;
        }
  
        .axes {
          position: absolute;
          width: 100%;
          height: 100%;
  
          .x-axis,
          .y-axis {
            position: absolute;
            background: #888;
            z-index: 2;
          }
  
          .x-axis {
            height: 1px;
            width: 100%;
            top: 50%;
            left: 0;
          }
  
          .y-axis {
            width: 1px;
            height: 100%;
            top: 0;
            left: 50%;
          }
        }
      }
  
      &__download-button {
        margin-top: 5px;
        padding: 10px 20px;
        background-color: #6c6c6c;
        color: #fff;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.2s ease;
  
        &:hover {
          background-color: #555;
        }
      }
    }
  
    &__processing {
      color: #000000;
      width:30rem;
    }
  }
  